@import "../../variables.scss";
@media screen and (max-width: 860px) {
  .loading {

    left: 44vw;
  }
}
.loading {

  left: 44vw;
  height: calc(30vh - 70px);
  width: calc(37.5vw);
  font-size: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: absolute;
  background-color: transparent;
  backdrop-filter: blur(55px);
  z-index: 4;

  font-family: "Great Vibes", cursive;
  font-weight: 500;
  color: $primary-color;
}

@media screen and (max-width: 860px) {
  .loading {
    height: calc(50vh);
    width: 100%;
  }
}
